*,
*::before,
*::after {
  box-sizing: border-box; }

body {
  margin: 0;
  padding: 0;
  font-size: 12px;
  color: #666;
  line-height: 1; }

.container {
  display: flex;
  flex-direction: column;
  width: 300px; }
  .container .header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 30px;
    padding: 0 10px;
    background: #f7f7f7;
    border-bottom: 1px solid #e6e6e6; }
    .container .header .name,
    .container .header .feedback {
      cursor: pointer; }
  .container .main {
    position: relative;
    padding: 10px 10px 0; }
    .container .main .item {
      margin-bottom: 12px; }
      .container .main .item .key {
        margin-bottom: 5px; }
      .container .main .item .value input,
      .container .main .item .value select {
        width: 100%;
        height: 25px;
        padding: 0 5px;
        outline: none; }
  .container .info {
    display: none;
    padding: 10px;
    color: red; }
  .container .footer {
    height: 30px;
    cursor: pointer;
    color: #fff; }
    .container .footer .start {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 100%;
      height: 100%;
      background: #4fc1e9; }
    .container .footer .stop {
      display: none;
      align-items: center;
      justify-content: center;
      width: 100%;
      height: 100%;
      background: #f6628f; }
  .container.live .main::after {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%; }
  .container.live .start {
    display: none; }
  .container.live .stop {
    display: flex; }
